import React, { useState, useEffect } from "react";

import {
    AutoForm,
    AutoField,
    ErrorField,
    SubmitField,
    LongTextField
} from "uniforms-bootstrap4";
import Ajv from "ajv";
import { JSONSchemaBridge } from "uniforms-bridge-json-schema";

import { v4 as uuidv4 } from 'uuid';

import { userCraft } from "../pages/login"
import useDictionary from "../hooks/useDictionary";

import Container from 'react-bootstrap/Container'
import Nav from 'react-bootstrap/Nav'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'



const ajv = new Ajv({ allErrors: true });

const getLocalForageDataByKeys = (keys) => {
    return Promise.all(["currenttask", ...keys].map(key => {
        return userCraft.getItem(key)
            .then(value => {
                return { [key]: value }
            })
            .catch(error => {
                console.log(error)
                return { [key]: null }
            })
    }))
        .then(arr => { return Object.assign(...arr) })
}

function createValidator(uniformSchema) {
    const validator = ajv.compile(uniformSchema);

    return input => {
        validator(input);

        if (validator.errors && validator.errors.length) {
            let errorMessage = { details: validator.errors };
            throw errorMessage;
        }
    };
}


function LocalForageUniforms(props) {
    const [isReady, setReady] = useState(false);
    const [formInitialValues, setFormInitialValues] = useState({});
    const [currentTask, setCurrentTask] = useState(null);
    const [formDictionaryTitles, setFormDictionaryTitles] = useState({});
    const [formDictionaryTypes, setFormDictionaryTypes] = useState({});
    const dictionary = useDictionary();

    useEffect(() => {
        // userCraft.clear().then(function() { console.log('Database is now empty.');})
        // .catch(function(err) { console.log(err); });

        let allDictionaryItems = [];
        dictionary.edges.forEach(item => {
            allDictionaryItems.push(item.node);
        });

        let formDictionaryTitlesObject = {}
        let formDictionaryTypesObject = {}

        Object.keys(props.uniformSchema.properties).map(propertiesKey => {
            formDictionaryTitlesObject[propertiesKey] = allDictionaryItems.filter(dictionaryItem => dictionaryItem.dictionaryId === propertiesKey)[0]?.comment || "Показатель";
            formDictionaryTypesObject[propertiesKey] = allDictionaryItems.filter(dictionaryItem => dictionaryItem.dictionaryId === propertiesKey)[0]?.type || "text";
        })

        setFormDictionaryTitles(formDictionaryTitlesObject);
        setFormDictionaryTypes(formDictionaryTypesObject);


        getLocalForageDataByKeys(props.uniformSchema?.properties ? Object.keys(props.uniformSchema.properties) : [])
            .then(initialValues => {
                //     console.log(initialValues);
                let { currenttask: currenttask, ...formInitialValues } = initialValues;
                //     console.log(currenttask, formInitialValues);
                setCurrentTask(currenttask);
                setFormInitialValues(formInitialValues);
                setReady(true)
            }).catch(error => {
                console.log(error) // do sth with error
            })
    }, [])

    console.log(props);

    if (isReady && !props.uniformSchema?.columns) return <Container>
        <Nav fill className="justify-content-center">
            <Nav.Item><small>{currentTask}</small></Nav.Item>
            <Nav.Item><small>{props.uniformSchema.title}</small></Nav.Item>
        </Nav>
        <OneColumnUniform
            formDictionaryTitles={formDictionaryTitles}
            formDictionaryTypes={formDictionaryTypes}
            formInitialValues={formInitialValues}
            currentTask={currentTask}
            uniformSchema={props.uniformSchema}
        />
    </Container>


    if (isReady && props.uniformSchema?.columns && props.uniformSchema.columns.length > 1) return <Container>
        <Nav fill className="justify-content-center">
            <Nav.Item><small>{currentTask}</small></Nav.Item>
            <Nav.Item><small>{props.uniformSchema.title}</small></Nav.Item>
        </Nav>
        <ManyColumnsUniforms
            formDictionaryTitles={formDictionaryTitles}
            formDictionaryTypes={formDictionaryTypes}
            formInitialValues={formInitialValues}
            formColumns={props.uniformSchema.columns}
            currentTask={currentTask}
            uniformSchema={props.uniformSchema}
        />
    </Container>


    return null
}

export default LocalForageUniforms

function ManyColumnsUniforms(props) {
    const [isReady, setReady] = useState(false);
    const [model, setModel] = useState({
        // currentcompanyfullname: "ПАО Газпром",
        // currentcompanyreporturl: "http://www.gazprom.ru",
        // currentcompanyunitofmoney: "млрд. руб."
    });
    const [newproperties, setNewproperties] = useState({});
    const [newrequired, setRequired] = useState([]);

    let currentTask = props.currentTask;


    useEffect(() => {

        let newproperties = {};
        let newrequired =[]
        let modelWithValues = {};
        console.log(props.formInitialValues);

        Object.keys(props.uniformSchema.properties).map(propertiesKey => {
            props.formColumns.map(columnTitle => {
                //        console.log(propertiesKey, columnTitle, props.uniformSchema.properties[propertiesKey]  )
                let tableCellName = propertiesKey + '_|_' + columnTitle;
                newproperties[tableCellName] = props.uniformSchema.properties[propertiesKey];
                newrequired.push(tableCellName);
                //       modelWithValues[propertiesKey + '_|_' + columnTitle] = Math.floor(Math.random() * 1000);
            })
        }
        );
        console.log(newproperties);

        //    console.log(props);

        Object.keys(props.formInitialValues || {}).forEach(dictionaryId => {
            let dictionaryIdObject = props.formInitialValues[dictionaryId];
            Object.keys(dictionaryIdObject || {}).forEach(uid => {
                if (!!dictionaryIdObject[uid]?.currenttask && dictionaryIdObject[uid].currenttask === currentTask) {
                    //        console.log(dictionaryIdObject[uid].currenttask);
                    let period = dictionaryIdObject?.[uid]?.period;
                    let value = dictionaryIdObject?.[uid]?.value;
                    modelWithValues[dictionaryId + '_|_' + period] = value; //Math.floor(Math.random() * 1000);
                }
            })
        });
        //      console.log(modelWithValues);

        // Object.keys(foundInitialValues || {}).forEach(item => {
        //     formik.setFieldValue(item, foundInitialValues[item])
        // });
        setRequired(newrequired);
        setNewproperties(newproperties);
        setModel(modelWithValues);
        setReady(true);
    }, [])

    console.log(props);
    // console.log(props.uniformSchema.properties);

    function submitUniform(values) {
        console.log(values);
        let dataObjects = {};
        Object.keys(props.formInitialValues).map(rowsItem => {
            //    console.log(rowsItem);
            dataObjects[rowsItem] = { ...props.formInitialValues[rowsItem] };
            Object.keys(values).map(valuesKey => {
                if (valuesKey.includes(rowsItem)) {
                    let nameBlocks = valuesKey.split('_|_');
                    //        console.log(nameBlocks[1]);
                    //        console.log(values[valuesKey]);

                    let keyIninitialValues = null
                    !!props.formInitialValues[rowsItem] && Object.keys(props.formInitialValues[rowsItem]).forEach(element => {
                        let currentObject = props.formInitialValues[rowsItem][element];
                        if ((currentObject.currenttask === currentTask) && (currentObject.period === nameBlocks[1])) {
                            keyIninitialValues = element;
                        }
                    });
                    if (!!keyIninitialValues) {
                        dataObjects[rowsItem][keyIninitialValues] =
                        {
                            currenttask: currentTask,
                            period: nameBlocks[1],
                            context: 'Default',
                            value: values[valuesKey]
                        };
                        console.log(dataObjects[rowsItem]);
                    } else {
                        dataObjects[rowsItem][uuidv4()] =
                        {
                            currenttask: currentTask,
                            period: nameBlocks[1],
                            context: 'Default',
                            value: values[valuesKey]
                        };
                        console.log(dataObjects[rowsItem]);
                    }
                }
            })
        })
        //  console.log(dataObjects);

        let matches = []
        for (let i in Object.keys(dataObjects)) {
            let valueKey = Object.keys(dataObjects)[i];
            // matches will be an array of promises
            matches.push(userCraft.setItem(valueKey, dataObjects[valueKey]))
        }
        Promise.all(matches).then(() => { setReady(true) }) //console.log('Form data saved!')   
    }

    let schema = { ...props.uniformSchema, properties: newproperties, required: newrequired };
    let uniformWithValidator = new JSONSchemaBridge(schema, createValidator(schema));

    console.log(model);

    return <Container>
        {isReady ?
            <AutoForm schema={uniformWithValidator} model={model} onSubmit={input => submitUniform(input)} className="m-3">
                <Row>
                    <Col>Показатели</Col>
                    {props.formColumns.map(columnTitle => <Col key={columnTitle}>{columnTitle}</Col>)}
                </Row>

                {Object.keys(props.uniformSchema.properties).map(propertyKey =>
                    <Row key={propertyKey}>
                        <Col>{props.formDictionaryTitles[propertyKey]}</Col>
                        {props.formColumns.map(columnTitle => <Col key={columnTitle}><AutoField name={propertyKey + '_|_' + columnTitle} key={propertyKey + '_|_' + columnTitle} /></Col>)}
                    </Row>
                )}

                {Object.keys(newproperties).map(propertyKey => <Row key={propertyKey}><ErrorField name={propertyKey} key={propertyKey} /></Row>)}

                <SubmitField />
            </AutoForm>
            :
            null}
    </Container>
}


function OneColumnUniform(props) {
    const [isReady, setReady] = useState(false);
    const [model, setModel] = useState({
        // currentcompanyfullname: "ПАО Газпром",
        // currentcompanyreporturl: "http://www.gazprom.ru",
        // currentcompanyunitofmoney: "млрд. руб."
    });

    let currentTask = props.currentTask;

    // If you're curious, you can run this Effect
    useEffect(() => {
        // console.log({ formik });
        let modelWithValues = {};

        Object.keys(props.formInitialValues || {}).forEach(dictionaryId => {
            let dictionaryIdObject = props.formInitialValues[dictionaryId];
            Object.keys(dictionaryIdObject || {}).forEach(uid => {

                if (!!dictionaryIdObject[uid]?.currenttask && dictionaryIdObject[uid].currenttask === currentTask) {
                    // console.log(dictionaryIdObject[uid].currenttask);
                    let value = dictionaryIdObject?.[uid]?.value;
                    modelWithValues[dictionaryId] = value;
                    // console.log(dictionaryId, value, currentTask);
                }
            })
        });
        setModel(modelWithValues);
        setReady(true);
    }, [])

    console.log(model);

    function submitUniform(values) {
        //     console.log(values);

        let dataObjects = {};
        Object.keys(props.formInitialValues).map(rowsItem => {
            console.log(rowsItem);
            dataObjects[rowsItem] = { ...props.formInitialValues[rowsItem] };
            console.log(rowsItem);
            Object.keys(values).map(valuesKey => {
                if (valuesKey.includes(rowsItem)) {
                    //   console.log(props.formInitialValues[rowsItem]);
                    let keyIninitialValues = null
                    !!props.formInitialValues[rowsItem] && Object.keys(props.formInitialValues[rowsItem]).forEach(element => {
                        let currentObject = props.formInitialValues[rowsItem][element];
                        if (currentObject.currenttask === currentTask) { keyIninitialValues = element }
                    });
                    if (!!keyIninitialValues) {
                        dataObjects[rowsItem][keyIninitialValues] =
                        {
                            currenttask: currentTask,
                            context: 'Default',
                            value: values[valuesKey]
                        };
                        console.log(dataObjects[rowsItem]);
                    } else {
                        dataObjects[rowsItem][uuidv4()] =
                        {
                            currenttask: currentTask,
                            context: 'Default',
                            value: values[valuesKey]
                        };
                        console.log(dataObjects[rowsItem]);
                    }
                }
            })
        })
        //  console.log(dataObjects);

        let matches = []
        for (let i in Object.keys(dataObjects)) {
            let valueKey = Object.keys(dataObjects)[i];
            // matches will be an array of promises
            matches.push(userCraft.setItem(valueKey, dataObjects[valueKey]))
        }
        Promise.all(matches).then(() => { setReady(true) }) //console.log('Form data saved!')
    }

    let schema = { ...props.uniformSchema };
    let uniformWithValidator = new JSONSchemaBridge(schema, createValidator(schema));

    if (!isReady) return null

    return <div
        style={{
            margin: `0 auto`,
            maxWidth: 800,
            padding: '2rem'
        }}>
        <div className="m-3">
            <AutoForm
                schema={uniformWithValidator}
                model={model}
                onSubmit={input => submitUniform(input)}
            />
        </div>
    </div>
}