import React, { useState } from "react";

import LocalForageUniforms from '../components/LocalForageUniforms';

import Header from '../components/header';

import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'

import ButtonGroup from 'react-bootstrap/ButtonGroup'
import Button from 'react-bootstrap/Button'


let taskSlides = [
    {
        title: 'Сведения о проекте',
        type: 'object',
        properties: {
            currentcompanyfullname: { type: 'string', label: 'Название компании' },
            currentcompanyreporturl: { type: 'string', label: 'Название компании' },
            currentcompanyunitofmoney: { type: 'string', label: 'Ден. ед.' },
            additionalInfo: {
                type: 'string',
                label: "Комментарий",
                // uniforms: { component: 'LongTextField' }
            }
        },
        required: ['currentcompanyfullname', 'currentcompanyreporturl', "currentcompanyunitofmoney"]
    },
    {
        title: 'Структура баланса',
        type: 'object',
        columns: ["2017", "2018", "2019"],
        properties: {
            code2110_minfin_ru_66n: { type: 'integer', label: '' },
            code1100_minfin_ru_66n: { type: 'integer', label: '' },
            code1200_minfin_ru_66n: { type: 'integer', label: '' },
            code1300_minfin_ru_66n: { type: 'integer', label: '' },
            code1400_minfin_ru_66n: { type: 'integer', label: '' },
            code1500_minfin_ru_66n: { type: 'integer', label: '' },
            // additionalInfo: {
            //     type: 'string',
            //     label: "Комментарий",
            //     // uniforms: { component: 'LongTextField' }
            // }
        },
        required: ['code2110_minfin_ru_66n', 'code1100_minfin_ru_66n', "code1200_minfin_ru_66n", "code1300_minfin_ru_66n", "code1400_minfin_ru_66n", "code1500_minfin_ru_66n"]
    }

];

export default () => {
    const [page, setPage] = useState(0);
    const [show, setShow] = useState(true);
    const [slideObject, setSlideObject] = useState(taskSlides[0]);

    React.useEffect(() => {
        setShow(false);
        setSlideObject(taskSlides[page]);
        const timer = setTimeout(() => {
            setShow(true);
        }, 0);
        return () => clearTimeout(timer);
      }, [page]);

     console.log(taskSlides[page]);

       return <>
        <Header siteTitle={"Econolabs"} />

        <Row>
            <Col sm="3">
                <ButtonGroup vertical className="m-5" size="sm" style={{ with: "100%" }}>
                    {taskSlides.map((slideObject, index) =>
                        <Button key={index} variant="outline-secondary" onClick={() => setPage(index)}>{slideObject.title}</Button>
                    )}
                </ButtonGroup>
            </Col>
            <Col sm="9"><Container>
                {show ? <LocalForageUniforms uniformSchema={slideObject} /> : null}
            </Container></Col>
        </Row>

    </>
}